  </group>



  <group title="门面" v-show="haveStore">
    <x-input title="名称" name="store_name" placeholder="请输入名称"></x-input>
    <link-cell link="positionPick" class="lc">
      <span slot="body">地址</span>
      <span slot="footer">{{pickedAddress}}</span>
    </link-cell>

    <cell >
      <uploader slot="body" :count="1" :maxlength="3" @weui-input-change="handleFileInputChange">

        <span slot="title">门头照<icon name="info" @click="showD"></icon></span>
        <uploader-files slot="uploader-files">
          <uploader-file @click="deleteFile" image-url="http://shp.qpic.cn/weixinsrc_pic/pScBR7sbqjOBJomcuvVJ6iacVrbMJaoJZkFUIq4nzQZUIqzTKziam7ibg/"></uploader-file>
          <uploader-file @click="deleteFile" image-url="http://shp.qpic.cn/weixinsrc_pic/pScBR7sbqjOBJomcuvVJ6iacVrbMJaoJZkFUIq4nzQZUIqzTKziam7ibg/" has-status>
            <icon slot="status" name="warn"></icon>
          </uploader-file>
          <uploader-file @click="deleteFile" image-url="http://shp.qpic.cn/weixinsrc_pic/pScBR7sbqjOBJomcuvVJ6iacVrbMJaoJZkFUIq4nzQZUIqzTKziam7ibg/" has-status>
            <span slot="status">50%</span>
