    margin-top: 5px;
  }
  .lc .weui_cell_ft:after{
    content: " ";
    display: inline-block;
    -webkit-transform: rotate(45deg);
    transform: rotate(45deg);
    height: 6px;
    width: 6px;
    border-width: 2px 2px 0 0;
    border-color: #c8c8cd;
    border-style: solid;
    position: relative;
    top: -2px;
    top: -1px;
    margin-left: .3em;
  }
</style>
<script>
  const sampleImage = 'data:image/png;base64,';

  import {Masker,XInput,Address,Group,AddressChinaData,XTextarea,Switch,Confirm,Dialog} from 'vux/src/components'
  import {Uploader,UploaderFile,UploaderFiles,Icon,Cell,LinkCell} from './widegt'
    export default{
        data(){
            return{
              haveStore:false,
              show: false,
              showDia:false,
              pickedAddress:this.$parent.pickaddress,
              title1:'城市',
                msg:'hello vue',
                list: [{
                title: '洗颜新潮流！人气洁面皂排行榜',
                img: 'https://cdn.xiaotaojiang.com/uploads/82/1572ec37969ee263735262dc017975/_.jpg',

              }],
              value2: ['广东省', '深圳市', '南山区'],
              addressData: AddressChinaData,
            }
