          Masker,XInput,Address,Group,Cell,Uploader,UploaderFile,UploaderFiles,Icon,XTextarea,Switch,Confirm,Dialog,LinkCell
        },
      events:{

//        'address-picked-dispatch-changed' (address) {
//          console.log('address-picked-dispatch-children'+address);
//          this.pickedAddress = address;
//        }
      },
      methods: {
        deleteFile(){
          this.show=true;
        },
        changeData () {
          this.value2 = ['430000', '430400', '430407']
        },
        getName (value) {
          return value2name(value, AddressChinaData)
        },
        logHide (str) {

          console.log('on-hide', str)
        },
        logShow (str) {
          console.log('on-show')
        },
        handleFileInputChange(event) {
          const {files} = event.target;
          if (files.length > 0) {
            console.log(files[0].name)
            this.showAlert(`你选择了${files[0].name}`);
          }

        },

        onCancel () {
          this.show=false;
          console.log('on cancel')
        },
        onConfirm () {
          this.show=false;
          console.log('on confirm')
        },
        onHide () {
          this.show=false;
          console.log('on hide')
        },
        onShow () {
          console.log('on show')
        },
        showD(){
          this.showDia=true;
        }
      },

    }
</script>



