<template>
    <div>
      <div style="text-align:center;">
        余额:<countup :start-val="1" :end-val="1388" :duration="4" class="demo1"></countup>元

      </div>



    </div>
  <divider name="update">更新日期</divider>

  <scroller lock-x scrollbar-y use-pullup use-pulldown height="400px" @pullup:loading="loadMore" @pulldown:loading="refresh" :pullup-status.sync="pullupStatus" v-ref:scroller>
    <div class="box2">

      <cell title="slot content" inline-desc='link="/component/radio"'  v-for="i in n">
        <div slot="value">
          <span style="color: green">placeholder {{i}}</span>
        </div>
      </cell>

      <!--<p v-for="i in n">placeholder {{i}}</p>-->
    </div>
    <!--pullup slot-->
    <div slot="pullup" class="xs-plugin-pullup-container xs-plugin-pullup-up" style="position: absolute; width: 100%; height: 40px; bottom: -40px; text-align: center;">
      <span v-show="pullupStatus === 'default'"></span>
      <span class="pullup-arrow" v-show="pullupStatus === 'down' || pullupStatus === 'up'" :class="{'rotate': pullupStatus === 'up'}">↑</span>
      <span v-show="pullupStatus === 'loading'"><spinner type="ios-small"></spinner></span>
    </div>
  </scroller>


</template>
<style>
  @import './style.css';
  .demo1 {
    font-family: 'Source Sans Pro', Helvetica, Arial, sans-serif;
    font-size: 4em;
    color: #04BE02;
  }
  .box2-wrap {
    height: 300px;
    overflow: hidden;
  }
  .rotate {
    display: inline-block;
    transform: rotate(-180deg);
  }
  .pullup-arrow {
    transition: all linear 0.2s;
    color: #666;
    font-size: 25px;
  }
</style>
<script>
  import { Countup,Divider,Scroller,Spinner,Cell} from 'vux/src/components'
    export default{

      components: {


        Countup,
        Divider,
        Scroller,
        Spinner,
        Cell

      },
      data(){
        return{
          msg:'hello vue',
          n: 10,
          n1: 10,
          pullupEnabled: true,
          pullupStatus: 'default'
        }
      },
      methods: {
        loadMore (uuid) {
          setTimeout(() => {
            this.n += 10
          this.$nextTick(() => {
            this.$broadcast('pullup:reset', uuid)
        })
        }, 2000)
        },
        refresh (uuid) {
          setTimeout(() => {
            this.n = 10
          this.$nextTick(() => {
            this.$broadcast('pulldown:reset', uuid)
        })
        }, 2000)
        },
        changePullupStatus (enabled) {
          if (enabled) {
            this.$broadcast('pullup:enable', this.$refs.scroller.uuid)
            this.pullupEnabled = true
          } else {
            this.$broadcast('pullup:disable', this.$refs.scroller.uuid)
            this.pullupEnabled = false
          }
        },
        loadMore1 (uuid) {
          setTimeout(() => {
            this.n1 += 10
          this.$nextTick(() => {
            this.$broadcast('pullup:reset', uuid)
          if (this.n1 >= 30) {
            this.$broadcast('pullup:disable', uuid)
            console.log('No more data, Pullup disabled!')
          }
        })
        }, 2000)
        }
      },
    }
</script>



