<template>

      <iframe width="100%" :height="height" name="ifr" id="ifr" src="http://m.amap.com/picker/?key=607227f10c0db402f8a29e5df7153a0e"></iframe>

</template>
<style>

</style>

<script>

    export default{
        data(){
            return{
                msg:'hello vue',
                height:document.documentElement.clientHeight,
                pickAddress:''
            }
        },
        components:{

        },
      ready (){
        var fth=this;
        const iframe=document.querySelector("#ifr").contentWindow;
       // console.log('123');

        setTimeout(function(){
          iframe.postMessage('hello','http://m.amap.com/picker/');
        },500)

        window.addEventListener("message", function(e){

         // console.log('e.data.name:'+e.data.name);
          fth.pickAddress=e.data.name;
          console.log(fth.msg);
          fth.$dispatch('address-picked-dispatch',e.data.name);
          window.history.back();
        }, false);

        window.addEventListener("DOMContentLoaded", function(){
          console.log('DOMContentLoaded');
        }, false);

      },


//        'address-broadcast' () {
//
//          this.$parent.pickaddress=this.pickAddress;
//         // this.hideDialog = !this.hideDialog
//        }
    //  },
    }


</script>



